










































import {Vue, Component, Watch} from 'vue-property-decorator'

@Component
export default class ObjectChangeDetectionCaveats extends Vue {
    numbers = [1, 2, 3, 4, 5]
    todos = [
        {
            event: '吃饭',
            isComplete: true
        }, {
            event: '喝水',
            isComplete: false
        }, {
            event: '睡觉',
            isComplete: true
        }
    ]

    toChangeValue(index) {
        this.$set(this.numbers, index, Math.random().toString().slice(2, 3))
    }
}
