module.exports = "<!--\r\n1. 变异方法\r\n通过这些方法改变数组，会触发视图重新渲染\r\npush()\r\npop()\r\nshift()\r\nunshift()\r\nsplice()\r\nsort()\r\nreverse()\r\n\r\n2. 替换数组\r\nexample1.items = example1.items.filter(function (item) {\r\n  return item.message.match(/Foo/)\r\n})\r\n\r\n你可能认为这将导致 Vue 丢弃现有 DOM 并重新渲染整个列表。幸运的是，事实并非如此。Vue 为了使得 DOM 元素得到最大范围的重用而实现了一些智能的、启发式的方法，所以用一个含有相同元素的数组去替换原来的数组是非常高效的操作。\r\n\r\n3. 改变数组元素的值\r\narr[index] = new Value 不生效\r\n\r\n可通过以下方式修改\r\nVue.set(vm.items, indexOfItem, newValue)\r\n// Array.prototype.splice\r\nvm.items.splice(indexOfItem, 1, newValue)\r\n-->\r\n\r\n\r\n<template>\r\n    <div>\r\n        <div class=\"case-item\">\r\n            <h4>1. v-for 数组</h4>\r\n            <el-button v-for=\"(item, index) in arr\"><span>{{ item.name }}</span></el-button>\r\n            <el-button type=\"primary\" @click=\"toReverse\">reverse</el-button>\r\n        </div>\r\n\r\n        <div class=\"case-item\">\r\n            <h4>2. 替换数组</h4>\r\n            <el-button v-for=\"(item, index) in arr\"><span>{{ item.name }}</span></el-button>\r\n            <el-button type=\"primary\" @click=\"toReplace\">替换数组</el-button>\r\n        </div>\r\n\r\n        <div class=\"case-item\">\r\n            <h4>3. 改变数组元素的值</h4>\r\n            <el-button v-for=\"(item, index) in arr\" @click=\"toChangeValue(index)\"><span>{{ item.name }}-修改值</span>\r\n            </el-button>\r\n        </div>\r\n    </div>\r\n</template>\r\n\r\n<script lang=\"ts\">\r\n    import {Vue, Component, Watch} from 'vue-property-decorator'\r\n\r\n    @Component\r\n    export default class ArrayChangeDetection extends Vue {\r\n        arr = [{name: 1}, {name: 2}, {name: 3}, {name: 4}]\r\n\r\n        toReplace() {\r\n            this.arr = this.arr.filter((item, index) => index > 0)\r\n        }\r\n\r\n        toReverse() {\r\n            this.arr.reverse()\r\n        }\r\n\r\n        toChangeValue(index) {\r\n            Vue.set(this.arr, index, {name: Math.random().toString(36).substr(2, 2)})\r\n        }\r\n    }\r\n</script>\r\n\r\n\r\n\r\n";